.serviceBox2 {
    text-align: center;
    padding: 40px 16px 40px;
    border: 1px solid #F0F4F5 ;
    transition: all 0.3s ease 0s;
    position: relative;
    background-color: #F0F4F5;
    z-index: 1;
    min-height: 230px;
    margin-bottom: 30px;
}

.serviceBox2:after,
.serviceBox2:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: all 0.5s ease 0s;
}

.serviceBox2:after {
    border-bottom: 1px solid #d7d7d7;
    border-top: 1px solid #d7d7d7;
    transform: scaleX(0);
    transform-origin: 0 100% 0;
    z-index: -1;
}

.serviceBox2:before {
    border-left: 1px solid #d7d7d7;
    border-right: 1px solid #d7d7d7;
    transform: scaleY(0);
    transform-origin: 100% 0 0;
    z-index: -1;
}

.serviceBox2:hover:after {
    transform: scaleX(1);
}

.serviceBox2:hover:before {
    transform: scaleY(1);
}

.serviceBox2 .service-icon i {
    font-size: 32px;
    line-height: 32px;
    color: #636363;
    transition: all 0.3s ease 0s;
}

.serviceBox2 .service-content h3 {
    margin-bottom: 15px;
    font-weight: bold;
}

.serviceBox2 .service-content h3 a {
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
    text-decoration: none;
}

.serviceBox2 .service-content h3 a:hover {}

.serviceBox2 .service-content p {
    line-height: 26px;
}

.serviceBox2 .read {
    margin-top: 20px;
}

.serviceBox2 .read a {
    border-radius: 50%;
    color: #636363;
    display: inline-block;
    line-height: 16px;
    text-align: center;
    text-decoration: none;
    transition: all 0.3s ease 0s;
    opacity: 1;
}

.serviceBox2 .read a:hover,
.serviceBox2:hover .service-icon i {
    color: #181818;
    border-color: #181818;
}

.serviceBox2:hover .read a {
    opacity: 1;
    transition: all 0.3s ease 0s;
}


